<template>
	<div class="wind-checklist">
		<w-cell :isLink="false"  v-for="(item, index) in currentOptions" :key="index" :disabled="item.disabled">
			<label class="wind-checklist-label" slot="cell-title" :class="{
                    'checkshell-position-right':align==='right'
                }">
                <div class="wind-checklist-checkshell" :style="{
                    'margin-right':align!=='right'?'10px':'0'
                }">
    				<span class="wind-checklist-span">
    					<input 
                            class="wind-checklist-input"
                            type="checkbox" 
                            :value="typeof item === 'object' ? item.value : item"
                            v-model="currentValue"
                            :disabled="isDisabled(getValue(item), item.disabled)"
                        >
    					<i class="icon-wind-check"></i>
    				</span>
                </div>
                <div class="wind-checklist-content">
                    <span class="wind-checklist-text" v-text="getName(item)"></span>
                    <span v-if="item && item.label !== undefined" class="wind-checklist-underLabel" v-text="item.label"></span>
                </div>
			</label>
		</w-cell>
	</div>
</template>
<script>
/**
 * w-checkList by shang 2018/4/6
 * @desc 多选及单选
 * @param {value} [Array] - 绑定值，支持双向绑定
 * @param {options}  [Array] - 选项参数 每个参数可以是string类型,也可以是obj类型
 * @options {value}  选项的值,对应参数value
 * @options {name}  选项显示的文本
 * @options {label}  [boolean] 选项显示的下标文字
 * @options {disabled}  选项是否禁用控件
 * @param {max} [Number] - 最大可选, max=1 开启raido模式
 * @param {min} [Number] - 最少必选
 * @param {align} [String] - 选项控件位置 right,left
 *
 * @event {on-change} 选项发生改变触发
 *
 * @methods {getCheckedOptions} 获取选中的选项的name和value
 * 
 * @example
 * <w-checkList :options="options1" v-model="value1" :align="align" @on-change="checkListChange"></w-checkList>
 * options1: ['鸣人', '卡卡西', '佐助'],
 * value1: ['卡卡西'],
 * 或
 * <w-checkList :options="options" v-model="value" :max="1" align="right" @on-change="checkListChange"></w-checkList>
 * options: [
        {
            value: 'itemValue1',
            name: '选项1非常长非常长非常长非常长非常长非常长非常长',
            label: '这是下标1非常长非常长非常长非常长非常长非常长非常长非常长'
        }, {
            value: 'itemValue2',
            name: '选项2'
        }, {
            value: 'itemValue3',
            name: '选项3',
            label: '这是下标2'
        }, {
            value: 'itemValue4',
            name: '选项4',
            disabled: true
        }
    ],
 * value: ['itemValue2'],
 */
import Cell from '../cell/index.js'
export default {
	name: 'w-checkList',
	props: {
		value: Array,
		options: {
			type: Array,
			required: true
		},
        max: Number,
        min: Number,
        align:{
            type: String,
            default: 'right'
        }
	},
	components: {
      'w-cell': Cell
    },
	data () {
		return {
			currentOptions: this.options,
			currentValue: this.value,
            radioValue: ''
		}
	},
    created () {
        // console.log('created')
    },
    beforeUpdate () {
        // console.log('beforeUpdate')
        if (this.isRadio) {
            const length = this.currentValue.length
            if (length > 1) {
                this.currentValue = [this.currentValue[length - 1]]
            }
            this.radioValue = this.currentValue
        }
    },
	computed: {
        isRadio () {
            return this.max === 1
        },
        _min () {
            var optionsLength = this.options.length
            var max = typeof this.max === 'undefined' ? optionsLength : this.max
            if (!this.min) {
                return 0
            } else {
                return Math.min(this.min,max,optionsLength)
            }
        }
	},
    watch: {
        value(val) {
            this.currentValue = val;
        },
        currentValue(val,oldVal) {
            if (!this.isRadio) {
                this.$emit('input', val)
                const deepVal = this.deepCopy(val)
                this.$emit('on-change', deepVal)
            } 
        },
        radioValue(val) {
            const _val = val? val : []
            const deepVal = this.deepCopy(_val)
            this.$emit('input', _val)
            this.$emit('on-change', deepVal)
        }
    },
    methods: {
        isDisabled(itemValue, disabled) {
            if (disabled) {
                return true
            }
            if (this.max > 1) {
                const checked = this.currentValue.indexOf(itemValue) !== -1
                return (!checked && this.currentValue.length === this.max) || ( checked && this.currentValue.length === this._min) 
            }
        },
        deepCopy(obj) {
            return JSON.parse(JSON.stringify(obj))
        },
        getValue(item) {
            return typeof item === 'object' ? item.value : item
        },
        getName(item) {
            return typeof item === 'object' ? item.name : item
        },
        getCheckedOptions() {
            const names = this.getNameArr(this.options,this.currentValue)
            return this.currentValue.map((item, index)=>{
                return {
                    value: item,
                    name: names[index]
                }
            })
        },
        getNameArr(options = [], values = []) {
            return values.map(value => {
                if (!options.length) {
                    return value
                }
                if (typeof options[0] === 'string') {
                    return value
                }
                const items = options.filter(item => {
                    return item.value === value
                })
                if (items.length) {
                    return items[0].name
                }
                return value
            })
        }
    }
}
</script>

<style lang="css">
.wind-checklist {
    line-height:initial;
}
.wind-checklist .wind-checklist-label {
	width:100%;
	display: flex;
}
.wind-checklist-label.checkshell-position-right {
    flex-direction: row-reverse;
}
.wind-checklist-content {
    width: 93%;
}
.wind-checklist-label .wind-checklist-text, .wind-checklist-label .wind-checklist-underLabel{
    /*margin-left:20px;*/
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
}
.wind-checklist-label .wind-checklist-underLabel {
    color: var(--cell-underlabel-color);
    display: block;
    font-size: 12px;
    margin-top: 6px;
}
.wind-checklist-label .wind-checklist-span {
    width: 22px;
    height: 22px;
    display: block;
    text-align: center;
    border: 1px solid #888;
    border-radius: 100%;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}
.wind-checklist-label .wind-checklist-span .wind-checklist-input {
    display: none;
}
.wind-checklist-label .wind-checklist-span .icon-wind-check {
	display: inline-block;
    vertical-align: middle;
    text-rendering: auto;
    width: 22px;
    height: 22px;
    transform: scale(0);
    transition: transform .2s;
    position: relative;
    top: 0px;
    left: -1px;
    text-align: center;
}
.wind-checklist-label .wind-checklist-span .icon-wind-check:before{
	width: 22px;
    height: 22px;
    display: inline-block;
    text-align: center;
    line-height: 22px;
    color:#26a2ff;
    font-size: 14px;
}

.wind-checklist-input:checked+.icon-wind-check {
    transform: scale(1);
}
.wind-checklist-input:checked+.icon-wind-check:before {
}
</style>



