<template>
	<div>
		<div class="checklist-title">基本使用</div>
		<div class="checklist-title">选择的选项值:{{value1}}</div>
		<w-checkList :options="options1" v-model="value1" :align="align" @on-change="checkListChange"></w-checkList>
		<div class="button-group">
			<w-button type='primary' @click="selectHandle('value1',['鸣人'])">选择第一个</w-button>
			<w-button type='primary' @click="selectHandle('value1',['鸣人','卡卡西','佐助'])">全选</w-button>
			<w-button type='primary'@click="align = align === 'left'?'right':'left'">切换文本位置</w-button>
		</div>
		<div class="checklist-title">raido模式</div>
		<div class="checklist-title">选择的选项值:{{value}}</div>
		<w-checkList :options="options" v-model="value" :max="1" align="right" @on-change="checkListChange"></w-checkList>
		<div class="checklist-title">最多选4个,最少选2个</div>
		<div class="checklist-title">选择的选项值:{{value2}}</div>
		<w-checkList :options="options2" v-model="value2" :max="4" :min="2" align="left" @on-change="checkListChange" ref="checkListObj"></w-checkList>
		<div class="button-group">
			<w-button type='primary' @click="getCheckedOptions">获取已选选项数据</w-button>
		</div>
	</div>
</template>

<script>

export default {
	name: 'ChecListDemo',
	data () {
		return {
			align: 'right',
			options: [
				{
					value: 'itemValue1',
					name: '选项1非常长非常长非常长非常长非常长非常长非常长',
					label: '这是下标1非常长非常长非常长非常长非常长非常长非常长非常长'
				}, {
					value: 'itemValue2',
					name: '选项2'
				}, {
					value: 'itemValue3',
					name: '选项3',
					label: '这是下标2'
				}, {
					value: 'itemValue4',
					name: '选项4',
					disabled: true
				}
			],
			value: ['itemValue2'],
			options1: ['鸣人', '卡卡西', '佐助'],
			value1: ['卡卡西'],
			options2: [
				{
					value: '北京',
					name: '北京'
				}, {
					value: '上海',
					name: '上海'
				}, {
					value: '广州',
					name: '广州'
				}, {
					value: '天津',
					name: '天津',
					disabled: false
				}, {
					value: '深圳',
					name: '深圳',
					disabled: false
				}
			],
			value2: [],
			test: ''
		}
	},
	created () {
	},
	methods: {
		selectHandle (value, newVal) {
			this[value] = newVal
		},
		checkListChange (currentValue) {
			console.log('checkListChange', currentValue)
		},
		getCheckedOptions () {
			var obj = this.$refs.checkListObj.getCheckedOptions()
			console.log(obj)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.checklist-title {
	text-align: left;
	padding-left:10px;
	padding-right:10px;
}
.button-group {
	padding-left:10px;
	padding-right:10px;
	margin-top:10px;
	margin-bottom:10px;
}
</style>



